.input-Page {
  display: flex;
  justify-content: center;
  font-family: "Gowun Dodum", sans-serif;
  align-items: center;
  height: 100vh;
  background-color: #D7ECD9;
  position: relative;
}

.img-container {
  display: flex; /* 이미지들을 가로로 일렬로 정렬하기 위해 추가 */
  justify-content: center; /* 이미지들을 수평 중앙으로 정렬하기 위해 추가 */
  position: absolute;
  top: 30px; /* 조정된 위치 */
}

.img-container img {
  width: 150px;
  height: 150px;
  margin: 0 10px; /* 좌우 여백 조정 */
  margin-right: 50px;
}

.input-container {
  position: relative;
  margin-top: 100px; /* 조정된 간격 */
}

.input-container .input-logo2 {
  margin-bottom: 20px;
}

.input-container .button-margin {
  margin-right: 20px;
}

.input-container .button-margin2 {
  margin-right: 20px;
  background-color: rgb(53, 172, 102);
}

.center {
  text-align: center;
}

.input-text {
  color: #000000;
  font-family: "Gowun Dodum", sans-serif;
  font-size: 18px;
  font-style: normal;
}

.input-logo {
  color: #000000;
  font-family: "Gowun Dodum", sans-serif;
  font-size: 36px;
  font-style: normal;
  text-align: center;
  opacity: 0;
  animation: fadeInAnimation 2s ease-in-out forwards;
}

.input-logo2 {
  color: #000000;
  font-family: "Gowun Dodum", sans-serif;
  margin-bottom: 20px;
  font-size: 30px;
  font-style: normal;
  text-align: center;
  opacity: 0;
  animation: fadeInAnimation 2s ease-in-out forwards;
}

.button-margin {
  margin-right: 20px;
}

.button-margin2 {
  margin-right: 20px;
  background-color: rgb(53, 172, 102);
}
